html,
body,
#root {
    height: 100%;
    margin: 0;
    padding: 0;
}

@font-face {
    font-family: "8 Bit Wonder";
    src: url("./8-BIT-WONDER.TTF");
}  

@font-face {
  font-family: "dogica";
  src: url("./dogica.ttf");
}  

@font-face {
  font-family: "dogicabold";
  src: url("./dogicabold.ttf");
}   

.homepage {
    background-color: #efebe8;
    display: flex;
    flex-direction: column;
    height: 100%;
    width: 100%;
}

.header {
    background-color: transparent;
    color: white;
    height: 50px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: fixed;
    top: 0;
    width: 100%;
}

.button {
    align-items: center;
    font-family: "8 Bit Wonder";  
    background-color: #3574c1;
    color: white;
    border: transparent;
    border-radius: 4px;
    padding: 6px;
    padding-bottom: 8px;
    margin-left: 30px;
    margin-top: 40px;
    cursor: pointer;
    text-decoration: none;
}

.button:hover {
    background-color: #1d78e1; /* change background color when hovered */
}

.button-container {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    margin-right: 40px;
    margin-top: 40px;
}
  
  .button-right {
    background-color: #e6e2e0;
    color: #51504f;
    border: none;
    border-radius: 4px;
    padding: 6px;
    padding-bottom: 8px;
    margin-left: 10px;
    font-family: "8 Bit Wonder";
    cursor: pointer;
    transition: background-color 0.2s ease-in-out;
    text-decoration: none;
}

.button-right:hover {
    background-color: #d9d4d2;
}

.button-alt {
    background-color: #e6e2e0;
    color: #51504f;
    border: none;
    border-radius: 4px;
    padding-top: 3px;
    padding-bottom: 3px;
    padding-left: 15px;
    padding-right: 15px;
    margin-left: 10px;
    font-family: "8 Bit Wonder";
    cursor: pointer;
    transition: background-color 0.2s ease-in-out;
}
  
.button-alt:hover {
    background-color: #d9d4d2;
}

.button-alt img {
    width: 22px;
    height: 22px;
}

.button-om {
    background-color: #e6e2e0;
    color: #51504f;
    border: none;
    border-radius: 4px;
    padding-top: 5px;
    padding-bottom: 5px;
    padding-left: 15px;
    padding-right: 15px;
    margin-left: 10px;
    font-family: "8 Bit Wonder";
    cursor: pointer;
    transition: background-color 0.2s ease-in-out;
}

.button-om:hover {
    background-color: #d9d4d2;
}

.button-om img {
    width: 18px;
    height: 18px;
}

.calendar-main {
  background-color: #efebe8;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start; /* Update this line */
  align-items: center;
  padding-top: 50px;
  padding-bottom: 90px;
}
  
.calendar-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  text-align: center;
}
  
.calendar-title {
  font-family: "8 Bit Wonder"; /* Add this line */
  font-size: 2.5rem; /* Update this line */
  color: black;
  text-decoration: none;
  background-color: transparent;
  border: none;
  cursor: default;
}

.featured-drop {
  font-family: "8 Bit Wonder"; /* Add this line */
  font-size: 1.5rem; /* Update this line */
  margin-bottom: 0rem;
  text-decoration: none;
  background-color: transparent;
  border: none;
}

.featured-grid-cell {
  position: relative;
  width: 15%;
  z-index: 50;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  cursor: pointer;
  opacity: 1; /* Add this line */
  transition: opacity 0.5s ease-in-out; /* Add this line */
  padding-bottom: 1rem;
}

.featured-grid-cell > img {
  z-index: 50;
  border-radius: 0.75rem;
  overflow: hidden;
  position: relative;
  image-rendering: pixelated;
  width: 100%;
  height: 100%;
  object-fit: cover;
  box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.25);
  transition: transform 0.2s cubic-bezier(0.25, 0.45, 0.45, 0.95), opacity 1s ease;
  transform-style: preserve-3d;
  backface-visibility: hidden;
}

.calendar-grid-container {
  padding-top: 0rem;
  display: grid;
  grid-template-columns: repeat(5, minmax(0, 1fr));
  grid-auto-rows: auto; /* Add this line */
  gap: 1rem;
  max-width: 80%;
  margin: 0 auto;
}

  .calendar-grid-cell {
    position: relative;
    width: 100%;
    z-index: 50;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    cursor: pointer;
    opacity: 1; /* Add this line */
    transition: opacity 0.5s ease-in-out; /* Add this line */
  }

  .calendar-grid-cell h3 {
    font-family: "dogicabold";
    font-size: .75rem;
    margin-bottom: 1rem;
  }

  .featured-grid-cell h3 {
    font-family: "dogicabold";
    font-size: 1rem;
    margin-bottom: 1rem;
    margin-top: 1.5rem;
  }
  
  .insc {
    font-family: "dogica";
    font-size: .75rem;
    margin-top: 1rem;
  }

  .date {
    font-family: "dogica";
    font-size: .75rem;
    margin-top: 0rem;
  }
  
  .calendar-grid-cell:hover > img {
    transform: scale3d(1.05, 1.05, 1);
  }  
  .featured-grid-cell:hover > img {
    transform: scale3d(1.05, 1.05, 1);
  }  
  
  .calendar-grid-cell > img {
    z-index: 50;
    border-radius: 0.75rem;
    overflow: hidden;
    position: relative;
    image-rendering: pixelated;
    width: 75%;
    height: 75%;
    object-fit: cover;
    box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.25);
    transition: transform 0.2s cubic-bezier(0.25, 0.45, 0.45, 0.95), opacity 1s ease;
    transform-style: preserve-3d;
    backface-visibility: hidden;
  }
  
  .social-icons {
    display: flex;
    justify-content: center;
    gap: 1rem;
  }
  
  .social-icons img {
    width: 20px;
    height: 20px;
  }

  .submit-container {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    font-family: "dogicabold";
    font-size: 1rem;
    font-weight: bold;
    margin-top: 2rem;
  }
  
  .submit-text {
    margin-right: 10px;
  }
  
  .submit-button {
    background-color: #3574c1;
    color: white;
    border: transparent;
    border-radius: 4px;
    padding: 6px;
    padding-bottom: 8px;
    font-family: "dogicabold";
    cursor: pointer;
    transition: background-color 0.2s ease-in-out;
    text-decoration: none;
  }
  
  .submit-button:hover {
    background-color: #1d78e1;
  }
  
  